<style lang="scss" scoped>
.pannel {
  display: flex;
  justify-content: space-around;

  .button {
    width: 40%;
    border: 0.5px solid rgba(0, 0, 0, 0.3);
    text-align: center;
    &.selected {
      background-color: #e9e9e9;
    }
  }
}
</style>
<template>
  <div class="pannel">
    <div
      v-for="(button, index) in buttons" v-bind:key="index"
      @click="onClick(index)"
      :class="{button: true, selected: index === selectedIndex}">
      {{button.text}}
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      buttons: [
        {
          type: 'caregiver',
          text: '간병인 찾기',
        },
        {
          type: 'patient',
          text: '환자 찾기',
        }
      ],
      selectedIndex: 0
    }
  },
  methods: {
    onClick(index) {
      this.selectedIndex = index
      this.$emit('click', this.buttons[this.selectedIndex].type)
    }
  }
}
</script>


