<style lang="scss" scoped>
.container {
  padding-left: 0px;
  padding-right: 0px;
  margin-left: 0px;
  margin-right: 0px;
}
.banner {
  width: 100%;
  height: 240px;

  &-image {
    width: 100%;
    height: 240px;
  }
}
</style>
<template>
  <div>
    <div class="container">
      <div v-if="isBanner">
        <HelperBanner />
      </div>
      <HelperPannelUserType @click="onClickUserType" />
      <HelperPannelFilter />
      <template v-if="isPatient">
        <HelperListPatient />
      </template>
      <template v-if="isCaregiver">
        <HelperListCaregiver />
      </template>
    </div>
  </div>
</template>
<script>
import HelperBanner from '@/components/helper-banner.vue'
import HelperPannelUserType from '@/components/helper-pannel-user-type.vue'
import HelperPannelFilter from '@/components/helper-pannel-filter.vue'
import HelperListPatient from '@/components/helper-list-patient.vue'
import HelperListCaregiver from '@/components/helper-list-caregiver.vue'

export default {
  components: {
    HelperBanner,
    HelperPannelUserType,
    HelperPannelFilter,
    HelperListPatient,
    HelperListCaregiver,
  },
  data() {
    return {
      isBanner: true,

      // contentType : 'caregiver', 'patient'
      contentType: 'patient',
    }
  },
  computed: {
    isPatient() {
      return this.contentType === 'patient'
    },
    isCaregiver() {
      return this.contentType === 'caregiver'
    }
  },
  methods: {
    onClickUserType(v) {
      console.log(v)
      this.contentType = v
    }
  }
}
</script>


