<style lang="scss" scoped>
.disabled {
  display: none;
  background-color: bisque;
  color: darkslategray;
  font-size: 24px;
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100vh;
  height: 100vh;
  z-index: 99999;

  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 720px) {
  .disabled {
    display: flex;
  }
}
</style>
<template>
  <div class="disabled">
    <p>display only mobile</p>
  </div>
</template>
<script>
export default {
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>


